import { useEffect, useState, useContext } from "react";

import { UserContext } from "context/userContext";
import Layout from "components/core/layout";
import axios from "services/axios";

const ConfirmTwoFactorComp = ({
  enableTwoFac,
  qrUrl,
  handleTokenChange,
  confirmTwoFactor,
  setEnableTwoFac,
}) => {
  return (
    <>
      {!enableTwoFac ? (
        <div>
          <img src={qrUrl} width="200px" />
          <p>Scan this QR code by Google Authenticator</p>
          <div className="row">
            <div className="col-md-2">
              <input
                className="form-control"
                maxLength={6}
                onChange={handleTokenChange}
              />
            </div>
            <div className="col-md-2">
              <button className="btn btn-primary" onClick={confirmTwoFactor}>
                Confirm
              </button>
            </div>
          </div>
        </div>
      ) : (
        <button
          type="button"
          className="btn btn-primary"
          onClick={() => setEnableTwoFac(true)}
        >
          Enable
        </button>
      )}
    </>
  );
};

function TwoFactorAuth() {
  const { userState, dispatch } = useContext(UserContext);

  const [enableTwoFac, setEnableTwoFac] = useState(false);
  const [qrUrl, setQrUrl] = useState("");
  const [token, setToken] = useState("");

  useEffect(() => {
    axios.get("/api/2fa").then((res) => {
      const {
        data: { qrcode },
      } = res.data;

      setQrUrl(qrcode);
    });
  }, []);

  const confirmTwoFactor = () => {
    if (token) {
      axios
        .post("/api/confirm/2fa", { token })
        .then((res) => {
          const {
            data: { verified },
          } = res.data;

          if (verified) {
            alert("Enable 2FA Success");
            dispatch({ type: "SET_USER", payload: { twofactor_auth: true } });
          }
        })
        .catch((e) => {
          const { status } = e.response;
          if (status === 403) alert("invalid token");
        });
    }
  };

  const handleTokenChange = (e) => {
    setToken(e.target.value);
  };

  return (
    <Layout>
      <div className="card">
        <div className="card-header">2FA</div>
        <div className="card-body">
          {userState.user.twofactor_auth ? (
            <p> 2FA enabled </p>
          ) : (
            <ConfirmTwoFactorComp
              enableTwoFac={enableTwoFac}
              qrUrl={qrUrl}
              handleTokenChange={handleTokenChange}
              confirmTwoFactor={confirmTwoFactor}
              setEnableTwoFac={setEnableTwoFac}
            />
          )}
        </div>
      </div>
    </Layout>
  );
}

export default TwoFactorAuth;
