import { useContext } from "react";
import { useHistory, Link, useParams } from "react-router-dom";
import { useFormik } from "formik";
import * as Yup from "yup";

import axios, { setHerder } from "services/axios";
import { UserContext } from "context/userContext";

const ErrorFeedback = ({ errors, touched, name }) => {
  return (
    <small className="text-danger">
      {errors[name] && touched[name] && errors[[name]]}
    </small>
  );
};

function Register() {
  const history = useHistory();
  const { dispatch } = useContext(UserContext);
  const { code } = useParams();

  const RegisterSchema = Yup.object().shape({
    firstname: Yup.string().required("Required"),
    lastname: Yup.string().required("Required"),
    email: Yup.string().email("Invalid email").required("Required"),
    username: Yup.string().required("Required"),
    password: Yup.string().required("Required"),
    passwordConfirm: Yup.string().oneOf(
      [Yup.ref("password"), null],
      "Passwords must match"
    ),
  });

  const onSubmit = async (values, { setSubmitting }) => {
    try {
      const {
        firstname,
        lastname,
        email,
        username,
        password,
        passwordConfirm,
        referralCode,
      } = values;

      const res = await axios.post("/api/register", {
        firstname,
        lastname,
        email,
        username,
        password,
        referral_code: referralCode,
      });

      const { statusCode, data } = res.data;

      if (statusCode === 201) {
        console.log("data ", data);
        setHerder(data.accessToken);
        dispatch({ type: "SET_USER", payload: data.user });
        dispatch({ type: "LOGIN" });
        return history.push("/home");
      }
    } catch (e) {
      const { message } = e.response.data;
      if (message) alert(message);
    }
  };

  const fm = useFormik({
    initialValues: {
      firstname: "",
      lastname: "",
      email: "",
      username: "",
      password: "",
      passwordConfirm: "",
      referralCode: "",
    },
    validationSchema: RegisterSchema,
    onSubmit: onSubmit,
  });

  const { errors, touched, handleSubmit, handleChange } = fm;

  return (
    <div className="register-page">
      <div className="card">
        <div className="card-body register-card-body">
          <p className="login-box-msg">Register</p>

          <form onSubmit={handleSubmit}>
            <div className="input-group mt-3">
              <input
                type="text"
                className="form-control"
                placeholder="Firstname"
                name="firstname"
                onChange={handleChange}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-user" />
                </div>
              </div>
            </div>
            <ErrorFeedback errors={errors} touched={touched} name="firstname" />

            <div className="input-group mt-3">
              <input
                type="text"
                className="form-control"
                placeholder="Lastname"
                name="lastname"
                onChange={handleChange}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-user" />
                </div>
              </div>
            </div>
            <ErrorFeedback errors={errors} touched={touched} name="lastname" />

            <div className="input-group mt-3">
              <input
                type="email"
                className="form-control"
                placeholder="Email"
                name="email"
                onChange={handleChange}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-envelope" />
                </div>
              </div>
            </div>
            <ErrorFeedback errors={errors} touched={touched} name="email" />

            <div className="input-group mt-3">
              <input
                type="text"
                className="form-control"
                placeholder="Username"
                name="username"
                onChange={handleChange}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-envelope" />
                </div>
              </div>
            </div>
            <ErrorFeedback errors={errors} touched={touched} name="username" />

            <div className="input-group mt-3">
              <input
                type="password"
                className="form-control"
                placeholder="Password"
                name="password"
                onChange={handleChange}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-lock" />
                </div>
              </div>
            </div>
            <ErrorFeedback errors={errors} touched={touched} name="password" />

            <div className="input-group mt-3">
              <input
                type="password"
                className="form-control"
                placeholder="Retype password"
                name="passwordConfirm"
                onChange={handleChange}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-lock" />
                </div>
              </div>
            </div>
            <ErrorFeedback
              errors={errors}
              touched={touched}
              name="passwordConfirm"
            />

            <div className="input-group mt-3">
              <input
                type="text"
                className="form-control"
                placeholder="Referral Code"
                name="referralCode"
                value={code}
                onChange={handleChange}
              />
              {console.log('ddd',code)}
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-user-plus" />
                </div>
              </div>
            </div>

            <br />
            <div className="row">
              <div className="col-12">
                <button type="submit" className="btn btn-primary btn-block">
                  Register
                </button>
              </div>
            </div>
            <Link to="/login" className="m-4">
              I already have a membership
            </Link>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Register;
