import { useContext, useEffect, useState } from "react";
import { useHistory, Link } from "react-router-dom";
import { useFormik } from "formik";

import LayoutUser from "components/core/layout/user";
import { UserContext } from "context/userContext";
import axios, { setHerder } from "services/axios";

export default function Login() {
  const history = useHistory();
  const { dispatch } = useContext(UserContext);

  const [token, setToken] = useState("");

  const getRefreshToken = async () => {
    const res = await axios.post("api/token");
    const {
      data: { accessToken, user },
    } = res.data;

    if (accessToken) {
      setHerder(accessToken);
      dispatch({ type: "LOGIN" });
      dispatch({ type: "SET_USER", payload: user });
      return history.push("/home");
    }
  };

  useEffect(() => {
    getRefreshToken();
  }, []);

  const initUser = (user) => {
    dispatch({ type: "SET_USER", payload: user });
    dispatch({ type: "LOGIN" });
  };

  const onSubmit = async (values, { setSubmitting }) => {
    try {
      const { username, password } = values;
      const res = await axios.post("/api/login", { username, password });

      const { statusCode, data } = res.data;

      if (!statusCode === 200) return alert("username or password invaild .");
      const { accessToken, user } = data;
      const { twofactor_auth } = data.user;

      setHerder(accessToken);
      if (twofactor_auth) {
        window.$("#twoFactorModal").modal("show");
        return false;
      }

      initUser(accessToken, user);
      return history.push("/home");
    } catch (e) {
      const { status } = e.response;
      if (status === 401) alert("username or password invaild .");
    }
  };

  const handleTokenChange = (e) => {
    setToken(e.target.value);
  };

  const validateToken = async () => {
    try {
      const res = await axios.post("/api/validate/totp", { token });
      const {
        data: { verified, user },
      } = res.data;

      if (!verified) return alert("Invaild token");
      window.$("#twoFactorModal").modal("hide");
      initUser(user);
      return history.push("/home");
    } catch (e) {
      console.log("e", e);
      const { status } = e.response;
      if (status === 403) alert("Invaild token");
    }
  };

  const fm = useFormik({
    initialValues: { username: "", password: "" },
    onSubmit: onSubmit,
  });

  const { errors, touched, handleSubmit, handleChange } = fm;

  return (
    <LayoutUser>
      <div
        className="d-flex p-4 justify-content-center "
        style={{ height: "100vh" }}
      >
        <div className="row py-5 mt-4 justify-content-center align-items-center">
          <div className="col-sm-5 col-md-6 mb-3 mb-md-0">
            <h1>CRYPTO BOTS TRADING</h1>
            <p className="text-muted mb-0">Create your trading bot now.</p>
          </div>

          <div className="col-sm-5 col-md-6">
            <form onSubmit={handleSubmit}>
              <div className="row">
                <div className="input-group col-lg-12 mb-4">
                  <div className="input-group-prepend">
                    <span className="input-group-text bg-white px-4 border-md border-right-0">
                      <i className="fa fa-user text-muted" />
                    </span>
                  </div>
                  <input
                    id="username"
                    type="text"
                    name="username"
                    placeholder="Username"
                    className="form-control bg-white border-left-0 border-md"
                    onChange={handleChange}
                  />
                  {errors.username && touched.username && errors.username}
                </div>
                <div className="input-group col-lg-12 mb-4">
                  <div className="input-group-prepend">
                    <span className="input-group-text bg-white px-4 border-md border-right-0">
                      <i className="fa fa-lock text-muted" />
                    </span>
                  </div>
                  <input
                    id="password"
                    type="password"
                    name="password"
                    placeholder="Password"
                    className="form-control bg-white border-left-0 border-md"
                    onChange={handleChange}
                  />
                  {errors.password && touched.password && errors.password}
                </div>
                <div className="form-group col-lg-12 mx-auto mb-0">
                  <button
                    type="submit"
                    href="#"
                    className="btn btn-primary btn-block py-2"
                  >
                    <span className="font-weight-bold">Login</span>
                  </button>
                </div>
                <div className="form-group col-lg-12 mx-auto d-flex align-items-center my-4">
                  <div className="border-bottom w-100 ml-5" />
                  <span className="px-2 small text-muted font-weight-bold text-muted">
                    OR
                  </span>
                  <div className="border-bottom w-100 mr-5" />
                </div>
                <div className="text-center w-100">
                  <p className="text-muted font-weight-bold">
                    Don't have an account?
                    <Link to="/register" className="text-primary ml-2">
                      Register
                    </Link>
                  </p>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>

      <div
        className="modal fade"
        id="twoFactorModal"
        tabIndex={-1}
        role="dialog"
        aria-labelledby="twoFactorModalTitle"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="twoFactorModalTitle">
                Confirm 2FA
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">Ã—</span>
              </button>
            </div>
            <div className="modal-body">
              <input
                className="form-control"
                maxLength={6}
                onChange={handleTokenChange}
              />
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-primary"
                onClick={validateToken}
              >
                Confirm
              </button>
            </div>
          </div>
        </div>
      </div>
    </LayoutUser>
  );
}
